	module.exports = {"data":{"postsJson":{"title":"Tried, Crucified & Buried","questionsTitle":"DIGGING DEEPER","display":"block","bookName":"John - ΚΑΤΑ ΙΩΑΝΝΗΝ","ref":"18:1-19:42","html":"<p>   <strong>18</strong><u>1</u>Ταῦτα εἰπὼν Ἰησοῦς ἐξῆλθεν σὺν τοῖς μαθηταῖς αὐτοῦ πέραν τοῦ χειμάρρου τοῦ Κεδρὼν ὅπου ἦν κῆπος, εἰς ὃν εἰσῆλθεν αὐτὸς καὶ οἱ μαθηταὶ αὐτοῦ.<br />   <u>2</u>Ἤιδει δὲ καὶ Ἰούδας ὁ παραδιδοὺς αὐτὸν τὸν τόπον, ὅτι πολλάκις συνήχθη Ἰησοῦς ἐκεῖ μετὰ τῶν μαθητῶν αὐτοῦ. <u>3</u>ὁ οὖν Ἰούδας λαβὼν τὴν σπεῖραν καὶ ἐκ τῶν ἀρχιερέων καὶ ἐκ τῶν Φαρισαίων ὑπηρέτας ἔρχεται ἐκεῖ μετὰ φανῶν καὶ λαμπάδων καὶ ὅπλων. <u>4</u>Ἰησοῦς οὖν εἰδὼς πάντα τὰ ἐρχόμενα ἐπʼ αὐτὸν ἐξῆλθεν καὶ λέγει αὐτοῖς·</p><h1>JESUS </h1><h2>τίνα ζητεῖτε;</h2><p><u>5</u>ἀπεκρίθησαν αὐτῷ·</p><h1>ROMAN SOLDIERS AND TEMPLE GUARDS </h1><h4>Ἰησοῦν τὸν Ναζωραῖον.</h4><p>λέγει αὐτοῖς·</p><h1>JESUS </h1><h2>ἐγώ εἰμι.</h2><p>εἱστήκει δὲ καὶ Ἰούδας ὁ παραδιδοὺς αὐτὸν μετʼ αὐτῶν. <u>6</u>ὡς οὖν εἶπεν αὐτοῖς· ἐγώ εἰμι, ἀπῆλθον εἰς τὰ ὀπίσω καὶ ἔπεσαν χαμαί. <u>7</u>Πάλιν οὖν ἐπηρώτησεν αὐτούς·</p><h1>JESUS </h1><h2>τίνα ζητεῖτε;</h2><p>οἱ δὲ εἶπαν·</p><h1>ROMAN SOLDIERS AND TEMPLE GUARDS </h1><h4>Ἰησοῦν τὸν Ναζωραῖον.</h4><p><u>8</u>ἀπεκρίθη Ἰησοῦς·</p><h1>JESUS </h1><h2>εἶπον ὑμῖν ὅτι ἐγώ εἰμι. εἰ οὖν ἐμὲ ζητεῖτε, ἄφετε τούτους ὑπάγειν·</h2><p><u>9</u>ἵνα πληρωθῇ ὁ λόγος ὃν εἶπεν ὅτι οὓς δέδωκάς μοι οὐκ ἀπώλεσα ἐξ αὐτῶν οὐδένα. <u>10</u>Σίμων οὖν Πέτρος ἔχων μάχαιραν εἵλκυσεν αὐτὴν καὶ ἔπαισεν τὸν τοῦ ἀρχιερέως δοῦλον καὶ ἀπέκοψεν αὐτοῦ τὸ ὠτάριον τὸ δεξιόν· ἦν δὲ ὄνομα τῷ δούλῳ Μάλχος. <u>11</u>εἶπεν οὖν ὁ Ἰησοῦς τῷ Πέτρῳ·</p><h1>JESUS </h1><h2>βάλε τὴν μάχαιραν εἰς τὴν θήκην· τὸ ποτήριον ὃ δέδωκέν μοι ὁ πατὴρ οὐ μὴ πίω αὐτό;</h2><p>   <u>12</u>Ἡ οὖν σπεῖρα καὶ ὁ χιλίαρχος καὶ οἱ ὑπηρέται τῶν Ἰουδαίων συνέλαβον τὸν Ἰησοῦν καὶ ἔδησαν αὐτὸν <u>13</u>καὶ ἤγαγον πρὸς Ἅνναν πρῶτον· ἦν γὰρ πενθερὸς τοῦ Καϊάφα, ὃς ἦν ἀρχιερεὺς τοῦ ἐνιαυτοῦ ἐκείνου·<u>14</u>ἦν δὲ Καϊάφας ὁ συμβουλεύσας τοῖς Ἰουδαίοις ὅτι συμφέρει ἕνα ἄνθρωπον ἀποθανεῖν ὑπὲρ τοῦ λαοῦ.<br />   <u>15</u>Ἠκολούθει δὲ τῷ Ἰησοῦ Σίμων Πέτρος καὶ ἄλλος μαθητής. ὁ δὲ μαθητὴς ἐκεῖνος ἦν γνωστὸς τῷ ἀρχιερεῖ καὶ συνεισῆλθεν τῷ Ἰησοῦ εἰς τὴν αὐλὴν τοῦ ἀρχιερέως, <u>16</u>ὁ δὲ Πέτρος εἱστήκει πρὸς τῇ θύρᾳ ἔξω. ἐξῆλθεν οὖν ὁ μαθητὴς ὁ ἄλλος ὁ γνωστὸς τοῦ ἀρχιερέως καὶ εἶπεν τῇ θυρωρῷ καὶ εἰσήγαγεν τὸν Πέτρον. <u>17</u>λέγει οὖν τῷ Πέτρῳ ἡ παιδίσκη ἡ θυρωρός·</p><h1>A SERVANT GIRL </h1><h4>μὴ καὶ σὺ ἐκ τῶν μαθητῶν εἶ τοῦ ἀνθρώπου τούτου;</h4><p>λέγει ἐκεῖνος·</p><h1>SIMON PETER </h1><h3>οὐκ εἰμί.</h3><p><u>18</u>εἱστήκεισαν δὲ οἱ δοῦλοι καὶ οἱ ὑπηρέται ἀνθρακιὰν πεποιηκότες, ὅτι ψῦχος ἦν, καὶ ἐθερμαίνοντο· ἦν δὲ καὶ ὁ Πέτρος μετʼ αὐτῶν ἑστὼς καὶ θερμαινόμενος.<br />   <u>19</u>Ὁ οὖν ἀρχιερεὺς ἠρώτησεν τὸν Ἰησοῦν περὶ τῶν μαθητῶν αὐτοῦ καὶ περὶ τῆς διδαχῆς αὐτοῦ. <u>20</u>ἀπεκρίθη αὐτῷ Ἰησοῦς·</p><h1>JESUS </h1><h2>ἐγὼ παρρησίᾳ λελάληκα τῷ κόσμῳ, ἐγὼ πάντοτε ἐδίδαξα ἐν συναγωγῇ καὶ ἐν τῷ ἱερῷ, ὅπου πάντες οἱ Ἰουδαῖοι συνέρχονται, καὶ ἐν κρυπτῷ ἐλάλησα οὐδέν. <u>21</u>τί με ἐρωτᾷς; ἐρώτησον τοὺς ἀκηκοότας τί ἐλάλησα αὐτοῖς· ἴδε οὗτοι οἴδασιν ἃ εἶπον ἐγώ.</h2><p><u>22</u>ταῦτα δὲ αὐτοῦ εἰπόντος εἷς παρεστηκὼς τῶν ὑπηρετῶν ἔδωκεν ῥάπισμα τῷ Ἰησοῦ εἰπών·</p><h1>A TEMPLE GUARD </h1><h4>οὕτως ἀποκρίνῃ τῷ ἀρχιερεῖ;</h4><p><u>23</u>ἀπεκρίθη αὐτῷ Ἰησοῦς·</p><h1>JESUS </h1><h2>εἰ κακῶς ἐλάλησα, μαρτύρησον περὶ τοῦ κακοῦ· εἰ δὲ καλῶς, τί με δέρεις;</h2><p><u>24</u>ἀπέστειλεν οὖν αὐτὸν ὁ Ἅννας δεδεμένον πρὸς Καϊάφαν τὸν ἀρχιερέα.<br />   <u>25</u>Ἦν δὲ Σίμων Πέτρος ἑστὼς καὶ θερμαινόμενος. εἶπον οὖν αὐτῷ·</p><h1>BYSTANDERS AT THE TRIAL </h1><h4>μὴ καὶ σὺ ἐκ τῶν μαθητῶν αὐτοῦ εἶ;</h4><p>ἠρνήσατο ἐκεῖνος καὶ εἶπεν·</p><h1>SIMON PETER </h1><h3>οὐκ εἰμί.</h3><p><u>26</u>λέγει εἷς ἐκ τῶν δούλων τοῦ ἀρχιερέως, συγγενὴς ὢν οὗ ἀπέκοψεν Πέτρος τὸ ὠτίον·</p><h1>BYSTANDERS AT THE TRIAL </h1><h4>οὐκ ἐγώ σε εἶδον ἐν τῷ κήπῳ μετʼ αὐτοῦ;</h4><p><u>27</u>πάλιν οὖν ἠρνήσατο Πέτρος, καὶ εὐθέως ἀλέκτωρ ἐφώνησεν.<br />   <u>28</u>Ἄγουσιν οὖν τὸν Ἰησοῦν ἀπὸ τοῦ Καϊάφα εἰς τὸ πραιτώριον· ἦν δὲ πρωΐ· καὶ αὐτοὶ οὐκ εἰσῆλθον εἰς τὸ πραιτώριον, ἵνα μὴ μιανθῶσιν ἀλλὰ φάγωσιν τὸ πάσχα. <u>29</u>Ἐξῆλθεν οὖν ὁ Πιλᾶτος ἔξω πρὸς αὐτοὺς καὶ φησίν·</p><h1>PONTIUS PILATE </h1><h4>τίνα κατηγορίαν φέρετε κατὰ τοῦ ἀνθρώπου τούτου;</h4><p><u>30</u>ἀπεκρίθησαν καὶ εἶπαν αὐτῷ·</p><h1>JEWISH LEADERS </h1><h4>εἰ μὴ ἦν οὗτος κακὸν ποιῶν, οὐκ ἄν σοι παρεδώκαμεν αὐτόν.</h4><p><u>31</u>εἶπεν οὖν αὐτοῖς ὁ Πιλᾶτος·</p><h1>PONTIUS PILATE </h1><h4>λάβετε αὐτὸν ὑμεῖς καὶ κατὰ τὸν νόμον ὑμῶν κρίνατε αὐτόν.</h4><p>εἶπον αὐτῷ οἱ Ἰουδαῖοι·</p><h1>JEWISH LEADERS </h1><h4>ἡμῖν οὐκ ἔξεστιν ἀποκτεῖναι οὐδένα·</h4><p><u>32</u>ἵνα ὁ λόγος τοῦ Ἰησοῦ πληρωθῇ ὃν εἶπεν σημαίνων ποίῳ θανάτῳ ἤμελλεν ἀποθνῄσκειν.<br />   <u>33</u>Εἰσῆλθεν οὖν πάλιν εἰς τὸ πραιτώριον ὁ Πιλᾶτος καὶ ἐφώνησεν τὸν Ἰησοῦν καὶ εἶπεν αὐτῷ·</p><h1>PONTIUS PILATE </h1><h4>σὺ εἶ ὁ βασιλεὺς τῶν Ἰουδαίων;</h4><p><u>34</u>ἀπεκρίθη Ἰησοῦς·</p><h1>JESUS </h1><h2>ἀπὸ σεαυτοῦ σὺ τοῦτο λέγεις ἢ ἄλλοι εἶπόν σοι περὶ ἐμοῦ;</h2><p><u>35</u>ἀπεκρίθη ὁ Πιλᾶτος·</p><h1>PONTIUS PILATE </h1><h4>μήτι ἐγὼ Ἰουδαῖός εἰμι; τὸ ἔθνος τὸ σὸν καὶ οἱ ἀρχιερεῖς παρέδωκάν σε ἐμοί· τί ἐποίησας;</h4><p><u>36</u>ἀπεκρίθη Ἰησοῦς·</p><h1>JESUS </h1><h2>ἡ βασιλεία ἡ ἐμὴ οὐκ ἔστιν ἐκ τοῦ κόσμου τούτου· εἰ ἐκ τοῦ κόσμου τούτου ἦν ἡ βασιλεία ἡ ἐμή, οἱ ὑπηρέται οἱ ἐμοὶ ἠγωνίζοντο ἂν ἵνα μὴ παραδοθῶ τοῖς Ἰουδαίοις· νῦν δὲ ἡ βασιλεία ἡ ἐμὴ οὐκ ἔστιν ἐντεῦθεν.</h2><p><u>37</u>εἶπεν οὖν αὐτῷ ὁ Πιλᾶτος·</p><h1>PONTIUS PILATE </h1><h4>οὐκοῦν βασιλεὺς εἶ σύ;</h4><p>ἀπεκρίθη ὁ Ἰησοῦς·</p><h1>JESUS </h1><h2>σὺ λέγεις ὅτι βασιλεύς εἰμι . ἐγὼ εἰς τοῦτο γεγέννημαι καὶ εἰς τοῦτο ἐλήλυθα εἰς τὸν κόσμον, ἵνα μαρτυρήσω τῇ ἀληθείᾳ· πᾶς ὁ ὢν ἐκ τῆς ἀληθείας ἀκούει μου τῆς φωνῆς.</h2><p><u>38</u>λέγει αὐτῷ ὁ Πιλᾶτος·</p><h1>PONTIUS PILATE </h1><h4>τί ἐστιν ἀλήθεια;</h4><p>Καὶ τοῦτο εἰπὼν πάλιν ἐξῆλθεν πρὸς τοὺς Ἰουδαίους καὶ λέγει αὐτοῖς·</p><h1>PONTIUS PILATE </h1><h4>ἐγὼ οὐδεμίαν εὑρίσκω ἐν αὐτῷ αἰτίαν. <u>39</u>ἔστιν δὲ συνήθεια ὑμῖν ἵνα ἕνα ἀπολύσω ὑμῖν ἐν τῷ πάσχα· βούλεσθε οὖν ἀπολύσω ὑμῖν τὸν βασιλέα τῶν Ἰουδαίων;</h4><p><u>40</u>ἐκραύγασαν οὖν πάλιν λέγοντες·</p><h1>THE CROWD </h1><h4>μὴ τοῦτον ἀλλὰ τὸν Βαραββᾶν.</h4><p>ἦν δὲ ὁ Βαραββᾶς λῃστής.<br />   <strong>19</strong><u>1</u>Τότε οὖν ἔλαβεν ὁ Πιλᾶτος τὸν Ἰησοῦν καὶ ἐμαστίγωσεν. <u>2</u>καὶ οἱ στρατιῶται πλέξαντες στέφανον ἐξ ἀκανθῶν ἐπέθηκαν αὐτοῦ τῇ κεφαλῇ καὶ ἱμάτιον πορφυροῦν περιέβαλον αὐτὸν <u>3</u>καὶ ἤρχοντο πρὸς αὐτὸν καὶ ἔλεγον·</p><h1>ROMAN SOLDIERS </h1><h4>χαῖρε ὁ βασιλεὺς τῶν Ἰουδαίων·</h4><p>καὶ ἐδίδοσαν αὐτῷ ῥαπίσματα. <u>4</u>Καὶ ἐξῆλθεν πάλιν ἔξω ὁ Πιλᾶτος καὶ λέγει αὐτοῖς·</p><h1>PONTIUS PILATE </h1><h4>ἴδε ἄγω ὑμῖν αὐτὸν ἔξω, ἵνα γνῶτε ὅτι οὐδεμίαν αἰτίαν εὑρίσκω ἐν αὐτῷ.</h4><p><u>5</u>ἐξῆλθεν οὖν ὁ Ἰησοῦς ἔξω, φορῶν τὸν ἀκάνθινον στέφανον καὶ τὸ πορφυροῦν ἱμάτιον. καὶ λέγει αὐτοῖς·</p><h1>PONTIUS PILATE </h1><h4>ἰδοὺ ὁ ἄνθρωπος.</h4><p>   <u>6</u>Ὅτε οὖν εἶδον αὐτὸν οἱ ἀρχιερεῖς καὶ οἱ ὑπηρέται ἐκραύγασαν λέγοντες·</p><h1>JEWISH LEADERS </h1><h4>σταύρωσον σταύρωσον.</h4><p>λέγει αὐτοῖς ὁ Πιλᾶτος·</p><h1>PONTIUS PILATE </h1><h4>λάβετε αὐτὸν ὑμεῖς καὶ σταυρώσατε· ἐγὼ γὰρ οὐχ εὑρίσκω ἐν αὐτῷ αἰτίαν.</h4><p><u>7</u>ἀπεκρίθησαν αὐτῷ οἱ Ἰουδαῖοι·</p><h1>JEWISH LEADERS </h1><h4>ἡμεῖς νόμον ἔχομεν καὶ κατὰ τὸν νόμον ὀφείλει ἀποθανεῖν, ὅτι υἱὸν θεοῦ ἑαυτὸν ἐποίησεν.</h4><p><u>8</u>Ὅτε οὖν ἤκουσεν ὁ Πιλᾶτος τοῦτον τὸν λόγον, μᾶλλον ἐφοβήθη, <u>9</u>καὶ εἰσῆλθεν εἰς τὸ πραιτώριον πάλιν καὶ λέγει τῷ Ἰησοῦ·</p><h1>PONTIUS PILATE </h1><h4>πόθεν εἶ σύ;</h4><p>ὁ δὲ Ἰησοῦς ἀπόκρισιν οὐκ ἔδωκεν αὐτῷ. <u>10</u>λέγει οὖν αὐτῷ ὁ Πιλᾶτος·</p><h1>PONTIUS PILATE </h1><h4>ἐμοὶ οὐ λαλεῖς; οὐκ οἶδας ὅτι ἐξουσίαν ἔχω ἀπολῦσαί σε καὶ ἐξουσίαν ἔχω σταυρῶσαί σε;</h4><p><u>11</u>ἀπεκρίθη αὐτῷ Ἰησοῦς·</p><h1>JESUS </h1><h2>οὐκ εἶχες ἐξουσίαν κατʼ ἐμοῦ οὐδεμίαν εἰ μὴ ἦν δεδομένον σοι ἄνωθεν· διὰ τοῦτο ὁ παραδούς μέ σοι μείζονα ἁμαρτίαν ἔχει.</h2><p><u>12</u>Ἐκ τούτου ὁ Πιλᾶτος ἐζήτει ἀπολῦσαι αὐτόν· οἱ δὲ Ἰουδαῖοι ἐκραύγασαν λέγοντες·</p><h1>JEWISH LEADERS </h1><h4>ἐὰν τοῦτον ἀπολύσῃς, οὐκ εἶ φίλος τοῦ Καίσαρος· πᾶς ὁ βασιλέα ἑαυτὸν ποιῶν ἀντιλέγει τῷ Καίσαρι.</h4><p><u>13</u>ὁ οὖν Πιλᾶτος ἀκούσας τῶν λόγων τούτων ἤγαγεν ἔξω τὸν Ἰησοῦν καὶ ἐκάθισεν ἐπὶ βήματος εἰς τόπον λεγόμενον Λιθόστρωτον, Ἑβραϊστὶ δὲ Γαββαθα. <u>14</u>ἦν δὲ παρασκευὴ τοῦ πάσχα, ὥρα ἦν ὡς ἕκτη. καὶ λέγει τοῖς Ἰουδαίοις·</p><h1>PONTIUS PILATE </h1><h4>ἴδε ὁ βασιλεὺς ὑμῶν.</h4><p><u>15</u>ἐκραύγασαν οὖν ἐκεῖνοι·</p><h1>THE CROWD </h1><h4>ἆρον ἆρον, σταύρωσον αὐτόν.</h4><p>λέγει αὐτοῖς ὁ Πιλᾶτος·</p><h1>PONTIUS PILATE </h1><h4>τὸν βασιλέα ὑμῶν σταυρώσω;</h4><p>ἀπεκρίθησαν οἱ ἀρχιερεῖς·</p><h1>JEWISH LEADERS </h1><h4>οὐκ ἔχομεν βασιλέα εἰ μὴ Καίσαρα.</h4><p><u>16</u>Τότε οὖν παρέδωκεν αὐτὸν αὐτοῖς ἵνα σταυρωθῇ.<br />   Παρέλαβον οὖν τὸν Ἰησοῦν,<u>17</u>καὶ βαστάζων ἑαυτῷ τὸν σταυρὸν ἐξῆλθεν εἰς τὸν λεγόμενον Κρανίου Τόπον, ὃ λέγεται Ἑβραϊστὶ Γολγοθα, <u>18</u>ὅπου αὐτὸν ἐσταύρωσαν, καὶ μετʼ αὐτοῦ ἄλλους δύο ἐντεῦθεν καὶ ἐντεῦθεν, μέσον δὲ τὸν Ἰησοῦν. <u>19</u>ἔγραψεν δὲ καὶ τίτλον ὁ Πιλᾶτος καὶ ἔθηκεν ἐπὶ τοῦ σταυροῦ· ἦν δὲ γεγραμμένον·<br />   Ἰησοῦς ὁ Ναζωραῖος ὁ βασιλεὺς τῶν Ἰουδαίων.<br />   <u>20</u>τοῦτον οὖν τὸν τίτλον πολλοὶ ἀνέγνωσαν τῶν Ἰουδαίων, ὅτι ἐγγὺς ἦν ὁ τόπος τῆς πόλεως ὅπου ἐσταυρώθη ὁ Ἰησοῦς· καὶ ἦν γεγραμμένον Ἑβραϊστί, Ῥωμαϊστί, Ἑλληνιστί. <u>21</u>ἔλεγον οὖν τῷ Πιλάτῳ οἱ ἀρχιερεῖς τῶν Ἰουδαίων·</p><h1>JEWISH LEADERS </h1><h4>μὴ γράφε· ὁ βασιλεὺς τῶν Ἰουδαίων, ἀλλʼ ὅτι ἐκεῖνος εἶπεν· βασιλεύς εἰμι τῶν Ἰουδαίων.</h4><p><u>22</u>ἀπεκρίθη ὁ Πιλᾶτος·</p><h1>PONTIUS PILATE </h1><h4>ὃ γέγραφα, γέγραφα.</h4><p>   <u>23</u>Οἱ οὖν στρατιῶται, ὅτε ἐσταύρωσαν τὸν Ἰησοῦν, ἔλαβον τὰ ἱμάτια αὐτοῦ καὶ ἐποίησαν τέσσαρα μέρη, ἑκάστῳ στρατιώτῃ μέρος, καὶ τὸν χιτῶνα. ἦν δὲ ὁ χιτὼν ἄραφος, ἐκ τῶν ἄνωθεν ὑφαντὸς διʼ ὅλου. <u>24</u>εἶπαν οὖν πρὸς ἀλλήλους·</p><h1>ROMAN SOLDIERS </h1><h4>μὴ σχίσωμεν αὐτόν, ἀλλὰ λάχωμεν περὶ αὐτοῦ τίνος ἔσται·</h4><p>ἵνα ἡ γραφὴ πληρωθῇ ἡ λέγουσα·<br />   διεμερίσαντο τὰ ἱμάτιά μου ἑαυτοῖς<br />   καὶ ἐπὶ τὸν ἱματισμόν μου ἔβαλον κλῆρον.<br />   Οἱ μὲν οὖν στρατιῶται ταῦτα ἐποίησαν.<br />   <u>25</u>Εἱστήκεισαν δὲ παρὰ τῷ σταυρῷ τοῦ Ἰησοῦ ἡ μήτηρ αὐτοῦ καὶ ἡ ἀδελφὴ τῆς μητρὸς αὐτοῦ, Μαρία ἡ τοῦ Κλωπᾶ καὶ Μαρία ἡ Μαγδαληνή. <u>26</u>Ἰησοῦς οὖν ἰδὼν τὴν μητέρα καὶ τὸν μαθητὴν παρεστῶτα ὃν ἠγάπα, λέγει τῇ μητρί·</p><h1>JESUS </h1><h2>γύναι, ἴδε ὁ υἱός σου.</h2><p><u>27</u>εἶτα λέγει τῷ μαθητῇ·</p><h1>JESUS </h1><h2>ἴδε ἡ μήτηρ σου.</h2><p>καὶ ἀπʼ ἐκείνης τῆς ὥρας ἔλαβεν ὁ μαθητὴς αὐτὴν εἰς τὰ ἴδια.<br />   <u>28</u>Μετὰ τοῦτο εἰδὼς ὁ Ἰησοῦς ὅτι ἤδη πάντα τετέλεσται, ἵνα τελειωθῇ ἡ γραφή, λέγει·</p><h1>JESUS </h1><h2>διψῶ.</h2><p><u>29</u>σκεῦος ἔκειτο ὄξους μεστόν· σπόγγον οὖν μεστὸν τοῦ ὄξους ὑσσώπῳ περιθέντες προσήνεγκαν αὐτοῦ τῷ στόματι. <u>30</u>ὅτε οὖν ἔλαβεν τὸ ὄξος ὁ Ἰησοῦς εἶπεν·</p><h1>JESUS </h1><h2>τετέλεσται,</h2><p>καὶ κλίνας τὴν κεφαλὴν παρέδωκεν τὸ πνεῦμα.<br />   <u>31</u>Οἱ οὖν Ἰουδαῖοι, ἐπεὶ παρασκευὴ ἦν, ἵνα μὴ μείνῃ ἐπὶ τοῦ σταυροῦ τὰ σώματα ἐν τῷ σαββάτῳ, ἦν γὰρ μεγάλη ἡ ἡμέρα ἐκείνου τοῦ σαββάτου, ἠρώτησαν τὸν Πιλᾶτον ἵνα κατεαγῶσιν αὐτῶν τὰ σκέλη καὶ ἀρθῶσιν. <u>32</u>ἦλθον οὖν οἱ στρατιῶται καὶ τοῦ μὲν πρώτου κατέαξαν τὰ σκέλη καὶ τοῦ ἄλλου τοῦ συσταυρωθέντος αὐτῷ· <u>33</u>ἐπὶ δὲ τὸν Ἰησοῦν ἐλθόντες, ὡς εἶδον ἤδη αὐτὸν τεθνηκότα, οὐ κατέαξαν αὐτοῦ τὰ σκέλη, <u>34</u>ἀλλʼ εἷς τῶν στρατιωτῶν λόγχῃ αὐτοῦ τὴν πλευρὰν ἔνυξεν, καὶ ἐξῆλθεν εὐθὺς αἷμα καὶ ὕδωρ.<u>35</u>καὶ ὁ ἑωρακὼς μεμαρτύρηκεν, καὶ ἀληθινὴ αὐτοῦ ἐστιν ἡ μαρτυρία, καὶ ἐκεῖνος οἶδεν ὅτι ἀληθῆ λέγει, ἵνα καὶ ὑμεῖς πιστεύσητε. <u>36</u>ἐγένετο γὰρ ταῦτα ἵνα ἡ γραφὴ πληρωθῇ· ὀστοῦν οὐ συντριβήσεται αὐτοῦ. <u>37</u>καὶ πάλιν ἑτέρα γραφὴ λέγει· ὄψονται εἰς ὃν ἐξεκέντησαν.<br />   <u>38</u>Μετὰ δὲ ταῦτα ἠρώτησεν τὸν Πιλᾶτον Ἰωσὴφ ὁ ἀπὸ Ἁριμαθαίας, ὢν μαθητὴς τοῦ Ἰησοῦ κεκρυμμένος δὲ διὰ τὸν φόβον τῶν Ἰουδαίων, ἵνα ἄρῃ τὸ σῶμα τοῦ Ἰησοῦ· καὶ ἐπέτρεψεν ὁ Πιλᾶτος. ἦλθεν οὖν καὶ ἦρεν τὸ σῶμα αὐτοῦ. <u>39</u>ἦλθεν δὲ καὶ Νικόδημος, ὁ ἐλθὼν πρὸς αὐτὸν νυκτὸς τὸ πρῶτον, φέρων μίγμα σμύρνης καὶ ἀλόης ὡς λίτρας ἑκατόν. <u>40</u>ἔλαβον οὖν τὸ σῶμα τοῦ Ἰησοῦ καὶ ἔδησαν αὐτὸ ὀθονίοις μετὰ τῶν ἀρωμάτων, καθὼς ἔθος ἐστὶν τοῖς Ἰουδαίοις ἐνταφιάζειν. <u>41</u>ἦν δὲ ἐν τῷ τόπῳ ὅπου ἐσταυρώθη κῆπος, καὶ ἐν τῷ κήπῳ μνημεῖον καινὸν ἐν ᾧ οὐδέπω οὐδεὶς ἦν τεθειμένος· <u>42</u>ἐκεῖ οὖν διὰ τὴν παρασκευὴν τῶν Ἰουδαίων, ὅτι ἐγγὺς ἦν τὸ μνημεῖον, ἔθηκαν τὸν Ἰησοῦν.</p>","path":"/349","Question1":"What can you learn from Jesus about how he faced suffering and injustice?","Question2":"What did Peter say when he was asked if he was one of Jesus' disciples? Why do you think he answered this way?","Question3":"Imagine how Peter felt in this story? When have you felt similar emotions? How did you respond?","Question4":"Spend some time talking to God about your personal experiences regarding suffering, injustice and denial. Allow God to lead you to an appropriate response.","prevPath":"/348","nextPath":"/350"}},"pathContext":{}}

